body {
  margin: 0;
  background-color: #fafafa;
}

img {
  position: absolute;
  max-width: 80%;
  top: 10%;
  left: 10%;
  background-color: white;
  padding: 2px;
  -webkit-box-shadow: 0 0 1vh black;
          box-shadow: 0 0 1vh black;
  border-radius: 5px;
}

img:empty
{
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@media screen and (orientation: portrait) {
  img {
    max-height: 80%;
    max-width: 90%;
  }
}

@media screen and (orientation: landscape) {
  img { max-height: 90%; }
}

#links {
  display: table;
  width: 100%;
  max-width: 500px;
  position: fixed;
  bottom: 0;
  right: 0;
}

.link {
  text-align: center;
  font-family: Arial;
  display: table-cell;
  font-size: .9em;
  opacity: .6;
  text-decoration: none;
  color: black;
  background-color: gray;
  padding: 5px;
  -webkit-transition: .7s;
  -o-transition: .7s;
  transition: .7s;
}

.link:hover {
  opacity: 1;
  color: white;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}



